
































import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class GameOver extends Vue {
  @Prop({ default: [] }) seats!: any[];
  @Prop() gameWin!: boolean;

  get title() {
    return this.gameWin ? "" : "";
  }

  getCardText(text: string) {
    switch (text) {
      case "wolf":
        return "";
      case "folk":
        return "";
      case "seer":
        return "";
      case "witch":
        return "";
      default:
        return "";
    }
  }
}
